import React, { useEffect, useRef, useState } from 'react';
import { MapContainer, TileLayer, useMap } from 'react-leaflet';
import L from 'leaflet';
import { Chart } from 'chart.js/auto';
import 'leaflet/dist/leaflet.css';
import './App.css';
import * as EL from 'esri-leaflet';

// Fix for default marker icon issues in Leaflet with React
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

function ArcGISFeatureLayer({ setTotalVehicles }) {
  const map = useMap();

  useEffect(() => {
    const featureLayer = EL.featureLayer({
      url: 'https://livingatlas.esri.in/server/rest/services/India/RTO_Vehicle_Category_Class_Norms_Fuel_2014_23/MapServer/0'
    });

    featureLayer.query().where("1=1").run((error, featureCollection) => {
      if (error) {
        console.error("Error fetching data:", error);
        return;
      }

      console.log('Feature Collection:', featureCollection); // Debugging line

      const total = featureCollection.features.reduce((sum, feature) => {
        const value = feature.properties.v_reg_till;
        console.log('Current Value:', value); // Debugging line
        return sum + value;
      }, 0);

      console.log('Total Vehicles Registered:', total); // Debugging line
      setTotalVehicles(total);
    });

    featureLayer.addTo(map);

    return () => {
      map.removeLayer(featureLayer);
    };
  }, [map, setTotalVehicles]);

  return null;
}

function App() {
  const [totalVehicles, setTotalVehicles] = useState(0);
  const chartRef = useRef(null);

  useEffect(() => {
    // Create a simple chart using Chart.js
    const ctx = document.getElementById('myChart').getContext('2d');
    if (chartRef.current) {
      chartRef.current.destroy(); // Destroy previous chart instance
    }
    chartRef.current = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
        datasets: [
          {
            label: '# of Votes',
            data: [12, 19, 3, 5, 2, 3],
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
            ],
            borderColor: [
              'rgba(255, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
            ],
            borderWidth: 1,
          },
        ],
      },
      options: {
        scales: {
          y: {
            beginAtZero: true,
          },
        },
      },
    });

    // Cleanup on component unmount
    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, []);

  return (
    <div className="container">
      <div className="panel">
        <h2>Total Vehicles Registered</h2>
        <p>{totalVehicles}</p>
      </div>
      <div className="map-container">
        <MapContainer center={[20.5937, 78.9629]} zoom={5} style={{ height: '100%', width: '100%' }}>
          <TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          />
          <ArcGISFeatureLayer setTotalVehicles={setTotalVehicles} />
        </MapContainer>
      </div>
      <div className="chart-container">
        <canvas id="myChart"></canvas>
      </div>
    </div>
  );
}

export default App;
